const API = {
	get(url, data = {}, method = 'GET'){ // get
		return new Promise((resolve,reject) => {
			// 
			url = `http://116.55.248.84:9090${url}`;
			uni.request({
				url,
				method: method,
				data: data,
				header:{ //
					'content-type': 'application/x-www-form-urlencoded',
				},
				success: (res) => {
					let data = res.data;
					if(data.code === 'success') {
						resolve(data);
					} else if (data.code === 'no-login') {
						uni.navigateTo({
							url: '../../login/login'
						});
					} else {
						uni.showToast({
							title: '' + data.msg,
							icon: 'none'
						});
						resolve(data);
					}	
				},
				fail: (err) => {
					uni.showToast({
						title:'',
						icon: 'none'
					});
					reject(err);
				}
			});
		});
	},
	post(url, data = {}){ 	// post
		return new Promise((resolve,reject) => {
			// 
			url = `http://116.55.248.84:9090${url}`;
			uni.request({
				url,
				data,
				method: 'POST',
				header:{ //
					'content-type': 'application/json'
				},
				success: (res) => {
					let data = res.data;
					if( data.code === 'success' ){
						resolve(data);
					} else if (data.code === 'no-login') {
						uni.navigateTo({
							url: '../../login/login'
						});
					}  else{
						uni.showToast({
							title: '' + data.message,
							icon: 'none'
						});
						resolve(data);
					}	
				},
				fail: (err) => {
					uni.showToast({
						title:'',
						icon: 'none'
					});
					reject(err);
				}
			});
		});
	},
	//
	upload(goodsCode=1){
		return new Promise((resolve,reject) => {
			let url = `http://116.55.248.84:9090/fuxi/common/upload-image?goodsCode=${goodsCode}`;
			// 
			uni.chooseImage({
				count: 1,
			    sizeType: ['original', 'compressed'], //
				sourceType: ['album', 'camera'], //
				success: (res) => {
					let imageSrc = res.tempFilePaths[0]
					uni.uploadFile({
						url: url,
						filePath: imageSrc,
						fileType: 'image',
						name: 'file',
						success: (res) => {
							res.imageSrc = imageSrc;
							resolve(res);
						},
						fail: (err) => {
							resolve(err);
						}
					});
				},
				fail: (err) => {
					reject(err);
				}
			})
		});
	},
	// 
	tab(url){
		return new Promise( (resolve,reject) => {
			uni.switchTab({
				url,
				success:(res) =>{
					resolve(res);
				},
				fail:(err) =>{
					reject(err);
				}
			});
		});
	},
	// 
	to(url){
		return new Promise( (resolve,reject) =>{
			uni.navigateTo({
				url,
				success:(res) =>{
					resolve(res);
				},
				fail:(err) =>{
					reject(err);
				}
			})
		});
	},
	//
	bank(delta = 1){
		uni.navigateBack({
			delta
		});
	},
	// 
	setStorage(key,data){
		return new Promise( (resolve,reject) =>{
			uni.setStorage({
				key,
				data,
				success:(res) =>{
					resolve(res);
				},
				fail:(err) =>{
					reject(err);
				}
			})
		})  
	},
	// 
	getStorage(key){
		return new Promise( (resolve,reject) =>{
			uni.getStorage({
				key,
				success:(res) =>{
					resolve(res);
				},
				fail:(err) =>{
					reject(err);
				}
			})
		})
	},
	//
	removeStorage(key){
		return new Promise( (resolve,reject) =>{
			uni.removeStorage({
				key,
				success:(res) =>{
					resolve(res);
				},
				fail:(err) =>{
					reject(err);
				}
			});
		})
	},
	//
	setTitle(title){
		uni.setNavigationBarTitle({
			title,
		});
	},
	disLeftNav(){
		let pages = getCurrentPages();
		let page = pages[pages.length - 1];
		let currentWebview = page.$getAppWebview();
		let titleObj = currentWebview.getStyle().titleNView;
		titleObj.autoBackButton = false;
		currentWebview.setStyle({
			titleNView: titleObj
		});
	}
};
export default API;